import React, { useEffect, useContext, useCallback } from 'react'
import { observer } from 'mobx-react'
import Store from './store/TableStore'
import { MobxProvider, useStore } from '../../Mobx/MobxConfig'
import {
  Button,
  DatePicker,
  Descriptions,
  Drawer,
  Table,
  Modal,
  Form,
  Dropdown,
  Menu,
  InputNumber,
  message
} from 'antd'
import { Input, Row, Col, Select } from 'antd'
import dayjs from 'dayjs'
import {
  EditFilled,
  DeleteFilled,
  EyeFilled,
  DownOutlined,
  EditOutlined,
  SaveOutlined,
  StopOutlined,
  SearchOutlined,
  CloseCircleTwoTone,
  UnlockOutlined,
  ReadOutlined
} from '@ant-design/icons'
const { RangePicker } = DatePicker
import Pagination from 'antd/es/pagination'
import '../../DataTable.less'
import { useTranslation } from 'react-i18next'
import moment from 'moment'
import { toJS } from 'mobx'
import { ActionDropDown } from '../../Components/ActionDropDown/ActionDropDown'
import { ActionButtonGroup } from '../../Components/ActionButtonGroup/ActionButtonGroup'

var searchInput // USED
var fake_state = {
  searchedColumn: null,
  searchText: null
}

export default function C3POTable(props) {
  return (
    <MobxProvider store={Store}>
      <LocalTable {...props} />
    </MobxProvider>
  )
}

const RenderForm = observer(() => {
  const { tableStore } = useStore(Store)

  if (
    tableStore.ActionComponent &&
    React.isValidElement(tableStore.ActionComponent) &&
    typeof tableStore.ActionComponent.type === 'function'
  ) {
    tableStore.ActionComponent.props.key = tableStore.primaryKey
    tableStore.ActionComponent.props.close = () =>
      tableStore.setModalVisibility(false)
    tableStore.ActionComponent.props.getHandler = tableStore.getRecord
    if (tableStore.ActionComponent.props.mode === 'read') {
      tableStore.ActionComponent.props.saveHandler = tableStore.readRecord
    } else if (tableStore.ActionComponent.props.mode === 'update-status') {
      tableStore.ActionComponent.props.saveHandler = tableStore.statusRecord
    } else if (tableStore.ActionComponent.props.mode === 'edit') {
      tableStore.ActionComponent.props.saveHandler = tableStore.updateRecord
    } else if (tableStore.ActionComponent.props.mode === 'delete') {
      tableStore.ActionComponent.props.saveHandler = tableStore.deleteRecord
    } else {
      tableStore.ActionComponent.props.saveHandler = tableStore.createRecord
    }
  }
  if (tableStore.actionView === 'drawer') {
    return (
      <Drawer
        width={tableStore.actionWidth}
        closable
        destroyOnClose
        visible={tableStore.actionFormVisible}
        title={tableStore.actionTitle}
        onClose={() => {
          tableStore.setModalVisibility(false)
        }}
      >
        {tableStore.ActionComponent}
      </Drawer>
    )
  } else if (tableStore.actionView === 'modal') {
    return (
      <Modal
        width={tableStore.actionWidth}
        title={tableStore.actionTitle}
        visible={tableStore.actionFormVisible}
        closable
        destroyOnClose
        footer={null}
        onCancel={() => tableStore.setModalVisibility(false)}
      >
        {tableStore.ActionComponent}
      </Modal>
    )
  } else {
    return tableStore.ActionComponent
  }
})

const LocalTable = observer((props) => {
  const { tableStore } = useStore(Store)

  const fetchMyAPI = useCallback(async () => {
    tableStore.setFetchData(props.fetchData)
    tableStore.setGetRecordHandler(props.getRecord)
    tableStore.setReadRecordHandler(props.readRecord)
    tableStore.setUpdateRecordHandler(props.updateRecord)
    tableStore.setUpdateStatusHandler(props.statusRecord)
    tableStore.setDeleteRecordHandler(props.deleteRecord)
    tableStore.setRowKey(props.rowKey)
    tableStore.setPage(props.perPage ? props.perPage : 10)
    tableStore.setBulkActionHandler(props.bulkActionRecord)
    await tableStore.fetchData()
  }, [props.c3poKey])

  const handleTableChange = async (pagination) => {
    tableStore.handleChangePage(pagination)
  }

  useEffect(() => {
    fetchMyAPI()
    return () => {
      tableStore.resetTableStore()
    }
  }, [fetchMyAPI])

  /* ### FILTER ### */

  const ActionButton = observer((props) => {
    const { type, record } = props
    if (type === 'dropdown') {
      return <ActionDropDown {...props} />
    } else {
      return <ActionButtonGroup {...props} />
    }
  })

  const renderColumns = (columns) => {
    if (columns) {
      return columns.map((column) => {
        if (column.hasOwnProperty('search_params')) {
          column = {
            ...column,
            ...getColumnSearchProps(
              column.dataIndex,
              column.search_params,
              column.title
            )
          }
        }

        //action columns
        if (column.action && column.action.length) {
          let { action, ...rest } = column
          const render = []
          column.action.forEach((action) => {
            //set default set disable function
            if (!action.setDisable) {
              action.setDisable = () => false
            }
            let Icon
            let onClick = (record) => {
              tableStore.setCurrentRecord(record)
              tableStore.setPrimaryKey(props.primaryKey(record))

              if (action.component) {
                tableStore.setComponent(
                  action.component,
                  action.view,
                  action.title,
                  action.width
                )
              }
            }

            switch (action.type) {
              case 'inlineEdit':
                Icon = EditOutlined || action.icon
                break
              case 'edit':
                Icon = EditFilled || action.icon
                break
              case 'view':
                Icon = EyeFilled || action.icon
                break
              case 'delete':
                Icon = DeleteFilled || action.icon
                break
              case 'read':
                Icon = ReadOutlined || action.icon
                break
              case 'update-status':
                Icon = UnlockOutlined || action.icon
                break
              default:
                Icon = null
            }

            render.push({
              type: 'default',
              key: action.type,
              Icon: Icon,
              text: action.text ? action.text : null,
              setDisable: action.setDisable,
              onClick: onClick
            })
          })
          return {
            ...rest,
            render: (text, record, index) => {
              return (
                <ActionButton
                  type={props.actionComponent}
                  data={render}
                  record={record}
                  text={props.dropdownText}
                />
              )
            }
          }
        }

        return column
      })
    }
    return []
  }

  const getColumnSearchProps = (dataIndex, search_params, title) => ({
    filterDropdown: ({
      setSelectedKeys,
      selectedKeys,
      confirm,
      clearFilters
    }) => (
      <div style={{ padding: 8 }}>
        {searchRender({
          selectedKeys,
          handleSearch,
          setSelectedKeys,
          search_params,
          title
        })}
        <Button
          type='primary'
          onClick={() =>
            handleSearch(selectedKeys, confirm, search_params, title)
          }
          icon={<SearchOutlined />}
          size='small'
          style={{ width: 90, marginRight: 8 }}
        >
          Search
        </Button>
        <Button
          onClick={() => handleReset(selectedKeys, clearFilters, search_params)}
          size='small'
          style={{ width: 90 }}
        >
          Reset
        </Button>
      </div>
    ),
    filterIcon: (filtered) => (
      <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />
    )
  })

  const handleReset = (selectedKeys, clearFilters, dataIndex) => {
    fake_state['searchText'] = selectedKeys[0]
    fake_state['searchedColumn'] = dataIndex
    tableStore.handleColumnFilter(fake_state, 'reset')
    clearFilters()
  }

  const searchRender = ({
    selectedKeys,
    handleSearch,
    setSelectedKeys,
    search_params,
    title
  }) => {
    return (
      <Input
        ref={(node) => {
          searchInput = node
        }}
        placeholder={`Search ${title}`}
        value={selectedKeys[0]}
        onChange={(e) =>
          setSelectedKeys(e.target.value ? [e.target.value] : [])
        }
        onPressEnter={() =>
          handleSearch(selectedKeys, confirm, search_params, title)
        }
        style={{ width: 188, marginBottom: 8, display: 'block' }}
      />
    )
  }

  const handleSearch = async (selectedKeys, confirm, search_params, title) => {
    fake_state['searchText'] = selectedKeys[0].replace(/\s+$/, '')
    fake_state['searchText'] = selectedKeys[0]
    fake_state['searchedColumn'] = search_params
    fake_state['title'] = title
    tableStore.handleColumnFilter(fake_state, 'add')
  }
  /* ### END FILTER ### */

  let { columns, ...rest } = props
  if (props.scrollAuto) {
    let autoCalculateWith = columns.length * 200
    rest = {
      scroll: { x: autoCalculateWith },
      ...rest
    }
  }
  if (props.expandableRows) {
    rest = {
      expandable: {
        expandedRowRender: (record) => {
          let item = []
          props.expandableRows.forEach((element, i) => {
            item.push(
              <Descriptions.Item
                bordered
                key={i + record[props.rowKey]}
                label={element.title}
                labelStyle={{ fontWeight: '600' }}
                span={Object.keys('span').length === 0 ? 1 : element.span}
                style={{
                  border: '1px solid lightgray',
                  borderSpacing: '1px 1px'
                }}
              >
                {record[element.key]}
              </Descriptions.Item>
            )
          })
          return (
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <Descriptions
                bordered
                key={record[props.rowKey]}
                title='ข้อมูลเพิ่มเติม'
              >
                {item}
              </Descriptions>
            </div>
          )
        }
      },
      ...rest
    }
  }

  let { bulkActionSelected, setBulkActionSelected } = tableStore
  const rowSelection = {
    selectedRowKeys: [...bulkActionSelected],
    onChange: setBulkActionSelected,
    hideSelectAll: true
  }

  return (
    <div>
      <RenderForm />
      <HeaderTable bulk={props.bulkAction} filter={props.queryHeader} />
      <FilterBar />
      <Table
        rowSelection={props.rowKey && props.rowSelect ? rowSelection : false}
        dataSource={tableStore.data}
        columns={renderColumns(columns)}
        loading={!tableStore.init || tableStore.isLoading}
        pagination={{
          position: ['bottomRight', 'none'],
          pageSize: tableStore.perPage,
          current: tableStore.currentPage,
          total: tableStore.totalItems
        }}
        onChange={handleTableChange}
        {...rest}
      />
    </div>
  )
})

const FilterBar = observer(() => {
  const { tableStore } = useStore(Store)

  const handleReset = (selectedKeys, clearFilters, dataIndex) => {
    fake_state['searchText'] = selectedKeys[0]
    fake_state['searchedColumn'] = dataIndex
    tableStore.handleColumnFilter(fake_state, 'reset')
    clearFilters()
  }

  return (
    <div
      style={{
        width: '100%',
        height: '62px',
        borderRadius: '18px',
        backgroundColor: 'rgb(205 219 81 / 24%)',
        borderStyle: 'dashed',
        borderWidth: '3px',
        borderColor: 'rgb(220 26 26 / 72%)',
        marginBottom: '12px',
        alignItems: 'center',
        display: tableStore.filter.length ? 'flex' : 'none',
        justifyContent: 'space-between'
      }}
    >
      <Row align='center'>
        <b style={{ marginLeft: '16px' }}>ค้นหาโดย : </b>
        {tableStore.filter.map((element) => {
          return (
            <div
              key={element.searchedColumn}
              style={{
                marginLeft: '10px',
                paddingInline: '8px',
                backgroundColor: 'rgb(122 252 210 / 52%)',
                borderRadius: '12px'
              }}
            >
              <div>
                {element.title}
                {' : '}
                {element.searchText}{' '}
                <CloseCircleTwoTone
                  key={element.searchedColumn}
                  onClick={() =>
                    handleReset(
                      element.searchText,
                      () => {},
                      element.searchedColumn
                    )
                  }
                  twoToneColor='#eb2f96'
                />{' '}
              </div>
            </div>
          )
        })}
      </Row>
      <Button
        danger
        type='link'
        onClick={() => tableStore.handleResetPage()}
        size='small'
        style={{ width: 90, marginRight: 8 }}
      >
        เคลียร์ทั้งหมด
      </Button>
    </div>
  )
})

const HeaderTable = observer(({ bulk, filter }) => {
  const { tableStore } = useStore(Store)

  const handleBulkActionOnClick = ({ key }) => {
    tableStore.bulkActionRecord(key)
  }

  const FilterButton = () => {
    let render = []

    // type: options / datepicker / button
    if (filter) {
      filter.forEach((E) => {
        if (E.type === 'button') {
          render.push(
            <Button
              key={E.label}
              icon={<E.icon />}
              onClick={() => {
                let filter = []
                Object.keys(E.queryParams).forEach((key) => {
                  filter.push({
                    searchText: E.queryParams[key],
                    searchedColumn: key,
                    title: E.label
                  })
                })
                tableStore.setBulkActionSelected([])
                tableStore.setFilter([...filter])
                tableStore.setSearchParams({
                  ...E.queryParams,
                  ...tableStore.headerStatus
                })
                tableStore.fetchData()
              }}
            >
              {E.label}
            </Button>
          )
        }

        if (E.type === 'options') {
          render.push(
            <E.component
              key={E.key}
              saveHandler={(val) => {
                let req = {}
                req[E.params] = val
                tableStore.setHeaderStatus({
                  ...tableStore.headerStatus,
                  ...req
                })
                tableStore.setSearchParams({
                  ...tableStore.searchParams,
                  ...req
                })
                tableStore.fetchData()
              }}
              onUpdateValue={
                tableStore.headerStatus === null
                  ? null
                  : tableStore.headerStatus[E.params]
              }
              onClear={() => {
                let clearParams = E.params
                let { headerStatus } = tableStore
                delete headerStatus[clearParams]
                tableStore.setHeaderStatus({ ...headerStatus })
              }}
            />
          )
        }
      })
    }
    return (
      <div style={{ display: 'inline-flex', flexWrap: 'wrap', gap: '12px' }}>
        {render}
      </div>
    )
  }

  const BulkButton = () => {
    const menu = {
      if(bulk) {
        return (
          <Menu onClick={handleBulkActionOnClick}>
            {bulk.options.map((e) => {
              return (
                <Menu.Item
                  key={e.value}
                  icon={<e.icon />}
                  disabled={e.disabled ? e.disabled : false}
                >
                  {e.label}
                </Menu.Item>
              )
            })}
          </Menu>
        )
      }
    }
    if (bulk) {
      return (
        <Dropdown
          overlay={menu}
          placement='bottomLeft'
          arrow
          disabled={!tableStore.bulkActionSelected.length}
        >
          <Button>{bulk.ButtonName}</Button>
        </Dropdown>
      )
    } else {
      return <div></div>
    }
  }

  return (
    <div>
      <div
        style={{
          display: bulk && filter.length ? 'flex' : 'none',
          justifyContent: 'space-between',
          marginBottom: '12px'
        }}
      >
        <BulkButton />
        <div>{FilterButton()}</div>
      </div>
      <div
        style={{
          width: '100%',
          height: '62px',
          borderRadius: '18px',
          backgroundColor: '#cde7fc',
          borderStyle: 'dashed',
          borderWidth: '3px',
          borderColor: 'rgb(220 26 26 / 72%)',
          marginBottom: '12px',
          alignItems: 'center',
          display: tableStore.bulkActionSelected.length ? 'flex' : 'none',
          justifyContent: 'space-between'
        }}
      >
        <Row align='center'>
          <b
            style={{ marginLeft: '16px' }}
          >{`Selected Total ${tableStore.bulkActionSelected.length} Records`}</b>
          {tableStore.bulkActionSelected.map((element) => {
            return (
              <div
                key={element}
                style={{
                  marginLeft: '10px',
                  paddingInline: '8px',
                  backgroundColor: 'rgb(122 252 210 / 52%)',
                  borderRadius: '12px'
                }}
              >
                <div>
                  {tableStore.rowKey}
                  {' : '}
                  {element}{' '}
                  <CloseCircleTwoTone
                    onClick={() =>
                      tableStore.handleResetBulkActionSelected(element)
                    }
                    twoToneColor='#eb2f96'
                  />{' '}
                </div>
              </div>
            )
          })}
        </Row>
        <Button
          danger
          type='link'
          onClick={() => tableStore.handleResetBulkActionSelected()}
          size='small'
          style={{ width: 90, marginRight: 8 }}
        >
          เคลียร์ทั้งหมด
        </Button>
      </div>
    </div>
  )
})
