import React, { useEffect, useContext, useCallback } from 'react'
import { observer } from 'mobx-react'
import Store from './store/R2D2Store'
import { MobxProvider, useStore } from '../../Mobx/MobxConfig'
import {
  Button,
  DatePicker,
  Drawer,
  Table,
  Modal,
  Form,
  Dropdown,
  Menu,
  InputNumber
} from 'antd'
import { Input, Row, Col, Select } from 'antd'
import dayjs from 'dayjs'
import {
  EditFilled,
  DeleteFilled,
  EyeFilled,
  DownOutlined,
  EditOutlined,
  SaveOutlined,
  StopOutlined,
  SearchOutlined,
  CloseCircleTwoTone
} from '@ant-design/icons'
const { RangePicker } = DatePicker
import Pagination from 'antd/es/pagination'
import '../../DataTable.less'
import { useTranslation } from 'react-i18next'
import moment from 'moment'
import { toJS } from 'mobx'

var searchInput // USED
var fake_state = {
  searchedColumn: null,
  searchText: null
}

export default function R2D2Table(props) {
  return (
    <React.Fragment>
      <MobxProvider store={Store}>
        <LocalTable {...props} />;
      </MobxProvider>
    </React.Fragment>
  )
}

const LocalTable = observer((props) => {
  const { r2d2Store } = useStore(Store)

  const fetchMyAPI = useCallback(async () => {
    r2d2Store.setFetchData(props.fetchData)
    await r2d2Store.fetchData()
  }, [])

  const handleTableChange = async (pagination) => {
    r2d2Store.handleChangePage(pagination)
  }

  useEffect(() => {
    fetchMyAPI()
  }, [fetchMyAPI])

  /* ### FILTER ### */

  const renderColumns = (columns) => {
    return columns.map((column) => {
      if (column.hasOwnProperty('search_params')) {
        column = {
          ...column,
          ...getColumnSearchProps(
            column.dataIndex,
            column.search_params,
            column.title
          )
        }
      }
      return column
    })
  }

  const getColumnSearchProps = (dataIndex, search_params, title) => ({
    filterDropdown: ({
      setSelectedKeys,
      selectedKeys,
      confirm,
      clearFilters
    }) => (
      <div style={{ padding: 8 }}>
        {searchRender({
          selectedKeys,
          handleSearch,
          setSelectedKeys,
          search_params,
          title
        })}
        <Button
          type='primary'
          onClick={() =>
            handleSearch(selectedKeys, confirm, search_params, title)
          }
          icon={<SearchOutlined />}
          size='small'
          style={{ width: 90, marginRight: 8 }}
        >
          Search
        </Button>
        <Button
          onClick={() => handleReset(selectedKeys, clearFilters, search_params)}
          size='small'
          style={{ width: 90 }}
        >
          Reset
        </Button>
      </div>
    ),
    filterIcon: (filtered) => (
      <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />
    )
  })

  const handleReset = (selectedKeys, clearFilters, dataIndex) => {
    fake_state['searchText'] = selectedKeys[0]
    fake_state['searchedColumn'] = dataIndex
    r2d2Store.handleColumnFilter(fake_state, 'reset')
    clearFilters()
  }

  const searchRender = ({
    selectedKeys,
    handleSearch,
    setSelectedKeys,
    search_params,
    title
  }) => {
    return (
      <Input
        ref={(node) => {
          searchInput = node
        }}
        placeholder={`Search ${title}`}
        value={selectedKeys[0]}
        onChange={(e) =>
          setSelectedKeys(e.target.value ? [e.target.value] : [])
        }
        onPressEnter={() =>
          handleSearch(selectedKeys, confirm, search_params, title)
        }
        style={{ width: 188, marginBottom: 8, display: 'block' }}
      />
    )
  }

  const handleSearch = async (selectedKeys, confirm, search_params, title) => {
    fake_state['searchText'] = selectedKeys[0].replace(/\s+$/, '')
    fake_state['searchText'] = selectedKeys[0]
    fake_state['searchedColumn'] = search_params
    fake_state['title'] = title
    r2d2Store.handleColumnFilter(fake_state, 'add')
  }
  /* ### END FILTER ### */

  let { columns, ...rest } = props
  if(props.scrollAuto) {
    let autoCalculateWith = columns.length*200;
    rest = { 
      scroll: {x: autoCalculateWith},
      ...rest
    }
  }

  return (
    <React.Fragment>
      <FilterBar />
      <Table
        dataSource={r2d2Store.data}
        columns={renderColumns(columns)}
        loading={!r2d2Store.init && r2d2Store.isLoading}
        pagination={{
          position: ['bottomRight', 'none'],
          pageSize: r2d2Store.perPage,
          current: r2d2Store.currentPage,
          total: r2d2Store.totalItems
        }}
        onChange={handleTableChange}
        {...rest}
      />
    </React.Fragment>
  )
})

const FilterBar = observer(() => {
  const { r2d2Store } = useStore(Store)

  const handleReset = (selectedKeys, clearFilters, dataIndex) => {
    fake_state['searchText'] = selectedKeys[0]
    fake_state['searchedColumn'] = dataIndex
    r2d2Store.handleColumnFilter(fake_state, 'reset')
    clearFilters()
  }

  return (
    <div
      style={{
        width: '100%',
        height: '62px',
        borderRadius: '18px',
        backgroundColor: 'rgb(205 219 81 / 24%)',
        borderStyle: 'dashed',
        borderWidth: '3px',
        borderColor: 'rgb(220 26 26 / 72%)',
        marginBottom: '12px',
        alignItems: 'center',
        display: r2d2Store.filter.length ? 'flex' : 'none',
        justifyContent: 'space-between'
      }}
    >
      <Row align='center'>
        <b style={{ marginLeft: '16px' }}>ค้นหาโดย : </b>
        {r2d2Store.filter.map((element) => {
          return (
            <div
              key={element.searchedColumn}
              style={{
                marginLeft: '10px',
                paddingInline: '8px',
                backgroundColor: 'rgb(122 252 210 / 52%)',
                borderRadius: '12px'
              }}
            >
              <div>
                {element.title}
                {' : '}
                {element.searchText}{' '}
                <CloseCircleTwoTone
                  onClick={() =>
                    handleReset(
                      element.searchText,
                      () => {},
                      element.searchedColumn
                    )
                  }
                  twoToneColor='#eb2f96'
                />{' '}
              </div>
            </div>
          )
        })}
      </Row>
      <Button
        danger
        type="link"
        onClick={() =>
          r2d2Store.handleResetPage()
        }
        size='small'
        style={{ width: 90, marginRight: 8 }}
      >
        เคลียร์ทั้งหมด
      </Button>
    </div>
  )
})
