import React from 'react'

import i18n from './i18n'

import { ConfigProvider } from 'antd'
import thTH from 'antd/es/locale/th_TH'
import enUS from 'antd/es/locale/en_US'

import { I18nextProvider } from 'react-i18next'
import 'antd/dist/antd.css'

import R2D2Component from './Pages/R2D2Table'
import C3POComponent from './Pages/C3POTable'

export const R2D2Table = (props) => {
  props.language &&
    i18n.addResourceBundle(props.lang, 'translation', props.language)
  return (
    <I18nextProvider i18n={i18n}>
      <ConfigProvider locale={props.lang === 'th' ? thTH : enUS}>
        <R2D2Component {...props} />
      </ConfigProvider>
    </I18nextProvider>
  )
}

export const C3POTable = (props) => {
  props.language &&
    i18n.addResourceBundle(props.lang, 'translation', props.language)
  return (
    <div key={props.c3poKey}>
      <I18nextProvider i18n={i18n}>
        <ConfigProvider locale={props.lang === 'th' ? thTH : enUS}>
          <C3POComponent {...props} />
        </ConfigProvider>
      </I18nextProvider>
    </div>
  )
}
