import React from 'react'
import { Button } from 'antd'

export const ActionButtonGroup = React.memo(({ data, record }) => {
  return data.map((Component) => {
    return (
      <Button
        type={Component.type}
        key={Component.key}
        icon={<Component.Icon />}
        onClick={() => Component.onClick(record)}
      >
        {Component.text}
      </Button>
    )
  })
})
