import React from "react";
import {Button,Dropdown, Menu } from 'antd';
import {
    DownOutlined,
} from "@ant-design/icons";


export const ActionDropDown = React.memo(({data, record, text}) => {

    const menu = (
        <Menu>
            {data.map(Component => {
                //console.log(Component);
                return (
                    <Menu.Item key={Component.key}
                               icon={<Component.Icon/>}
                               onClick={() => Component.onClick(record)}
                               disabled={Component.setDisable(record)}
                    >
                        {Component.text}
                    </Menu.Item>
                )
            })}
        </Menu>
    );
    return (
        <Dropdown overlay={menu}>
            <Button>
                {text} <DownOutlined/>
            </Button>
        </Dropdown>
    )
});