import { message } from 'antd'
import { action, makeObservable, observable, runInAction, toJS } from 'mobx'

class TableStore {
  init = false
  data = []
  totalItems = 0
  state = 'pending'
  rowKey = ''
  fetchSize = 5000
  filter = []
  searchParams = {}
  currentPage = 1
  perPage = 10
  totalPage = 100
  isLoading = true
  fetchData = () => {}
  currentRecord = null
  primaryKey = null
  ActionComponent = null
  actionView = null
  actionTitle = null
  actionWidth = null
  actionFormVisible = false
  getRecord = () => {}
  readRecord = () => {}
  statusRecord = () => {}
  updateRecord = () => {}
  createRecord = () => {}
  deleteRecord = () => {}
  bulkActionRecord = () => {}
  bulkActionSelected = []

  headerStatus = {}

  constructor() {
    makeObservable(this, {
      init: observable,
      data: observable,
      totalItems: observable,
      state: observable,
      rowKey: observable,
      fetchSize: observable,
      filter: observable,
      searchParams: observable,
      currentPage: observable,
      perPage: observable,
      totalPage: observable,
      isLoading: observable,
      currentRecord: observable,
      primaryKey: observable,
      ActionComponent: observable,
      actionView: observable,
      actionTitle: observable,
      actionWidth: observable,
      actionFormVisible: observable,

      getRecord: observable,
      readRecord: observable,
      statusRecord: observable,
      updateRecord: observable,
      createRecord: observable,
      deleteRecord: observable,

      bulkActionRecord: observable, // func
      bulkActionSelected: observable, // Object of Record Selected
      setBulkActionHandler: action,
      setBulkActionSelected: action,
      handleResetBulkActionSelected: action,

      setRowKey: action,
      setModalVisibility: action,
      setComponent: action,
      setInit: action,
      handleChangePage: action,
      handleResetPage: action,
      handleColumnFilter: action,
      setCurrentRecord: action,
      setPrimaryKey: action,

      setPage: action,

      setGetRecordHandler: action,
      setReadRecordHandler: action,
      setUpdateRecordHandler: action,
      setUpdateStatusHandler: action,
      setCreateRecordHandler: action,
      setDeleteRecordHandler: action,

      setSearchParams: action,
      setFilter: action,

      headerStatus: observable,
      setHeaderStatus: action,
      resetTableStore: action
    })
  }

  setPage = (_page) => {
    this.perPage = _page
  }

  handleChangePage = action((page) => {
    this.isLoading = true
    this.state = 'pending'
    this.currentPage = page.current
    this.perPage = page.pageSize
    this.fetchData()
  })

  handleColumnFilter = action((searchColumn, types) => {
    this.currentPage = 1
    this.bulkActionSelected = []
    let rmColumnFilter = this.filter.filter(
      ({ searchedColumn }) => searchedColumn !== searchColumn.searchedColumn
    )
    if (types == 'add') {
      this.filter = [...rmColumnFilter, searchColumn]
    }
    if (types == 'reset') {
      this.filter = [...rmColumnFilter]
      this.searchParams = {}
    }
    this.filter.forEach((ele) => {
      this.searchParams[ele.searchedColumn] = ele.searchText
    })
    if (this.headerStatus)
      if (Object.keys(this.headerStatus).length)
        this.searchParams = { ...this.searchParams, ...this.headerStatus }
    this.fetchData()
  })

  handleResetPage = () => {
    this.isLoading = true
    this.state = 'pending'
    this.currentPage = 1
    this.bulkActionSelected = []
    this.filter = []
    this.searchParams = {}
    this.headerStatus = null
    this.fetchData()
  }

  setInit(flag) {
    this.init = flag
  }

  setRowKey(_key) {
    this.rowKey = _key
  }

  setFilter(_filter = []) {
    this.filter = [..._filter]
  }

  setFetchData = action(async (func) => {
    this.fetchData = action(async () => {
      try {
        this.isLoading = true

        let self = this
        console.log(this.currentPage)
        function delayFetch() {
          return new Promise((resolve, reject) => {
            setTimeout(() => {
              resolve(func(self.currentPage, self.perPage, self.searchParams))
            }, 0)
          })
        }

        let result = await delayFetch()
        console.log(result.cur_pages - 1)
        if(result.data.length === 0 && ((result.cur_pages - 1) !== 0) ) {
          result = await func(result.cur_pages - 1, this.perPage, this.searchParams)
        }

        runInAction(() => {
          // console.log(result.data.length)
          // console.log(result.cur_pages)
          // console.log("test number convert",Number(this.cur_pages))
          // console.log(result.cur_pages -1)
          // console.log((result.cur_pages - 1) !== 0)
            this.setInit(true)
            this.data = [...result.data]
            this.totalItems = result.total_items
            this.totalPage = result.total_pages
            this.currentPage = result.cur_pages
            this.isLoading = false
            this.state = 'success'
        })
      } catch (e) {
        console.error(e)
        runInAction(() => {
          this.setInit(true)
          this.isLoading = false
          this.state = 'error'
        })
      }
    })
  })

  setCurrentRecord = (_record) => {
    this.currentRecord = _record
  }

  setPrimaryKey = (_key) => {
    this.primaryKey = _key
  }

  setComponent = (component, view, title, width = '50%') => {
    if (component) {
      this.ActionComponent = component
      this.actionView = view
      this.actionTitle = title
      this.actionWidth = width
      this.setModalVisibility(true)
    }
  }

  setModalVisibility = action((_visibility) => {
    this.actionFormVisible = _visibility
  })

  setGetRecordHandler = (func) => {
    this.getRecord = async () => {
      try {
        const record = await func(this.primaryKey)
        await runInAction(() => {
          this.currentRecord = record
        })
        return record
      } catch (e) {
        runInAction(() => {
          this.isLoading = false
        })
      }
    }
  }

  setReadRecordHandler = (func) => {
    this.readRecord = action(async (record) => {
      await func(record)
      runInAction(() => {
        this.isLoading = false
        this.setModalVisibility(false)
        this.fetchData()
      })
    })
  }

  setUpdateRecordHandler = (func) => {
    this.updateRecord = action(async (record) => {
      await func(record)
      runInAction(() => {
        this.setModalVisibility(false)
        this.fetchData()
      })
    })
  }

  setUpdateStatusHandler = (func) => {
    this.statusRecord = action(async (record) => {
      await func(record)
      runInAction(() => {
        this.isLoading = false
        this.setModalVisibility(false)
        this.fetchData()
      })
    })
  }

  setCreateRecordHandler = (func) => {
    this.createRecord = action(async (record) => {
      await func(record)
      runInAction(() => {
        this.isLoading = false
        this.setModalVisibility(false)
        this.fetchData()
      })
    })
  }

  setDeleteRecordHandler = (func) => {
    this.deleteRecord = action(async (record) => {
      await func(record)
      runInAction(() => {
        this.isLoading = false
        this.setModalVisibility(false)
        this.fetchData()
      })
    })
  }

  setBulkActionHandler = (func) => {
    this.bulkActionRecord = action(async (key) => {
      try {
        let records = this.bulkActionSelected
        await func(records, key)
        runInAction(() => {
          this.isLoading = false
          this.bulkActionSelected = []
          this.setModalVisibility(false)
          this.fetchData()
        })
      } catch (e) {
        console.log(e)
        runInAction(() => {
          message.error(e)
          this.isLoading = false
          this.setModalVisibility(false)
        })
      }
    })
  }

  setBulkActionSelected = (recordSelected) => {
    // find key in page
    // if true : Delete else merge
    let keyInPage = this.data.map((e) => e[this.rowKey]) // Key On Page Data
    let keySelectedInPage = this.data.filter((e) =>
      recordSelected.includes(e[this.rowKey])
    ) // Selected Key On Processing Page (return Obj Record)
    let getKeyMergeSelectedInPage = keySelectedInPage.map((e) => e[this.rowKey]) // get Selected Key On Processing Page from object above
    let keepKeyOtherPage = this.bulkActionSelected.filter(
      (e) => !keyInPage.includes(e)
    ) // Key in Other Page need keeping
    this.bulkActionSelected = [
      ...keepKeyOtherPage,
      ...getKeyMergeSelectedInPage
    ]
  }

  handleResetBulkActionSelected = (key) => {
    if (key) {
      this.bulkActionSelected = this.bulkActionSelected.filter((e) => e !== key)
    } else {
      this.bulkActionSelected = []
    }
  }

  setSearchParams = (_params = {}) => {
    this.searchParams = { ..._params }
  }

  setHeaderStatus = (_status = null) => {
    this.bulkActionSelected = [] // for change status case
    this.headerStatus = _status
  }

  resetTableStore = () => {
    this.init = false
    this.data = []
    this.totalItems = 0
    this.state = 'pending'
    this.rowKey = ''
    this.fetchSize = 5000
    this.filter = []
    this.searchParams = {}
    this.currentPage = 1
    this.perPage = 10
    this.totalPage = 100
    this.isLoading = true
    this.fetchData = () => {}
    this.currentRecord = null
    this.primaryKey = null
    this.ActionComponent = null
    this.actionView = null
    this.actionTitle = null
    this.actionWidth = null
    this.actionFormVisible = false
    this.getRecord = () => {}
    this.readRecord = () => {}
    this.statusRecord = () => {}
    this.updateRecord = () => {}
    this.createRecord = () => {}
    this.deleteRecord = () => {}
    this.bulkActionRecord = () => {}
    this.bulkActionSelected = []
    this.headerStatus = {}
  }
}

export default {
  tableStore: new TableStore()
}
