import { action, makeObservable, observable, runInAction, toJS } from 'mobx'

class R2D2Store {
  init = false
  data = []
  totalItems = 0
  state = 'pending'
  fetchSize = 5000
  filter = []
  searchParams = {}
  currentPage = 1
  perPage = 10
  totalPage = 100
  isLoading = true
  fetchData = () => {}

  constructor() {
    makeObservable(this, {
      init: observable,
      data: observable,
      totalItems: observable,
      state: observable,
      fetchSize: observable,
      filter: observable,
      searchParams: observable,
      currentPage: observable,
      perPage: observable,
      totalPage: observable,
      isLoading: observable,
      setInit: action,
      handleChangePage: action,
      handleResetPage: action,
      handleColumnFilter: action
    })
  }

  handleChangePage = action(async (page) => {
    this.isLoading = true
    this.state = 'pending'
    this.currentPage = page.current
    this.perPage = page.pageSize
    this.fetchData()
  })

  handleColumnFilter = action(async (searchColumn, types) => {
    let rmColumnFilter = await this.filter.filter(
      ({ searchedColumn }) => searchedColumn !== searchColumn.searchedColumn
    )
    if (types == 'add') {
      this.filter = [...rmColumnFilter, searchColumn]
    }
    if (types == 'reset') {
      this.filter = [...rmColumnFilter]
      this.searchParams = {}
    }
    this.filter.forEach((ele) => {
      this.searchParams[ele.searchedColumn] = ele.searchText
    })
    this.fetchData()
  })

  handleResetPage = () => {
    this.isLoading = true
    this.state = 'pending'
    this.currentPage = 1
    this.filter = []
    this.searchParams = {}
    this.fetchData()
  }

  setInit(flag) {
    this.init = flag
  }

  setFetchData = action(async (func) => {
    this.fetchData = action(async () => {
      try {
        const result = await func(
          this.currentPage,
          this.perPage,
          this.searchParams
        )
        runInAction(() => {
          this.setInit(true)
          this.data = [...result.data]
          this.totalItems = result.total_items
          this.totalPage = result.total_pages
          this.currentPage = result.cur_pages
          this.isLoading = false
          this.state = 'success'
        })
      } catch (e) {
        console.error(e)
        runInAction(() => {
          this.setInit(true)
          this.isLoading = false
          this.state = 'error'
        })
      }
    })
  })
}

export default {
  r2d2Store: new R2D2Store()
}
